/* Home Page CSS */

@media screen and (min-width: 517px) {
  
  .centerHomePage {
    text-align: center;
    width: 100%;
  }

  .homePageHeading {
    font-family: 'Lobster', cursive;
    font-size: 60px;
    color: #E4E6EB;
  }

  .homePageAboutMe {
    color: #E4E6EB;
    font-size: 22px;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5rem;
  }

  .resumeDownload {
    color: #E4E6EB;
    text-decoration: none;
    background-color: #242526;
    padding: 20px;
    font-size: 25px;
    font-family: 'Lobster';
    margin: 20px;
  }

  .resumeDownload:hover {
    background-color: rgba(196, 196, 196, 60%);
    cursor: pointer;
  }
}

@media screen and (max-width: 517px) {
  
  .centerHomePage {
    text-align: center;
    width: 100%;
  }

  .homePageHeading {
    font-family: 'Lobster', cursive;
    font-size: 40px;
    color: #E4E6EB;
  }

  .homePageAboutMe {
    color: #E4E6EB;
    font-size: 16px;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5rem;
  }

  .resumeDownload {
    color: #E4E6EB;
    text-decoration: none;
    background-color: #242526;
    padding: 12px;
    font-size: 20px;
    font-family: 'Lobster';
    margin: 20px;
  }
}

/* Project Page CSS */

@media screen and (min-width: 1000px) {
  .projectTable {
    table-layout: fixed;
    margin-top: 50px; 
    margin-left: 200px;
    margin-right: 200px;
    color: #E4E6EB;
    border: 25px;
  }

  .projectTable th {
    font-size: 30px;
  }

  .projectTable td {
    padding-bottom: 20px;
    padding-left: 20px;
    width: 45%;
    vertical-align: top;
  }

  .projectTable a{
    text-decoration: none;
    color: #E4E6EB;
  }

  .boldLink {
    font-weight: 700;
  }
}

